@mixin link--primary {
  
}

@mixin link--reversed-color {
  a {
    color: var(--text-color);

    &:hover {
      color: var(--link-color);  
    }  
  }
}



//-----------------------
//images
//-----------------------

@mixin object-fit {
  width: 100%;
  object-fit: cover;
}
//-----------------------
//functions
//-----------------------

//line limit

@mixin line--limit {    
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
